let a = 0;
$(document).ready(function () {
  function headerMQ() {
    let width = $(window).width();
    let height = $(window).height();

    //let widthInt = parseInt(width, 10);
    // let heightInt = parseInt(height, 10);
    // console.log(width);
    if (width <= 700) {
      // console.log(width);

      $(".innerHeaderW").css("transform", ` scale(${width / (width + 220)})`);

      //   $(".innerHeader").css("width", ` ${widthInt / 0.9}%`);
    } else {
      $(".innerHeaderW").css("transform", " scale(1)");
    }

    if (width < 750) {
      // console.log(width);

      $(" .images-wrapper").css("transform", ` scale(${width / 751})`);
      //   $(".innerHeader").css("width", ` ${widthInt / 0.9}%`);
    } else {
      $(".images-wrapper").css("transform", " scale(1)");
    }

    if (width <= 950) {
      // console.log(width);
      //  console.log("ddd");
      $(".slicesW").css("transform", ` scale(${width / (width + 220)}) `);
      //   $(".innerHeader").css("width", ` ${widthInt / 0.9}%`);
    } else {
      $(".slicesW").css("transform", " scale(1)");
    }

    //   if (width <= 850) {
    //       // console.log(width);
    //       if (a > 101) {
    //           a = -a;
    //       } else a += width / 849;
    //       $(".slicesH").css("left", ` ${-a}% `);
    //       //   $(".innerHeader").css("width", ` ${widthInt / 0.9}%`);
    //   } else {
    //       $(".slicesH").css("left", "0");
    //       a = 0;
    //   }

    if (height <= 900) {
      // console.log(width);
      //  console.log("ddd");
      $(".innerHeaderH").css("transform", ` scale(${height / 901}) `);
      //   $(".innerHeader").css("width", ` ${widthInt / 0.9}%`);
    } else {
      $(".innerHeaderH").css("transform", " scale(1)");
    }

    if (height <= 800) {
      // console.log(width);
      //  console.log("ddd");
      $(".slicesH").css("transform", ` scale(${height / (height + 250)}) `);
      //   $(".innerHeader").css("width", ` ${widthInt / 0.9}%`);
    } else {
      $(".slicesH").css("transform", " scale(1)");
    }

    //   if (height <= 900) {
    //       // console.log(width);
    //       //  console.log("ddd");
    //       $(".pizza__item").css(
    //           "height",
    //           `  `
    //       );
    //       //   $(".innerHeader").css("width", ` ${widthInt / 0.9}%`);
    //   } else {
    //      // $(".pizza__item").css("transform", " scale(1)");
    //   }
  }

  headerMQ();

  $(window).resize(headerMQ);

  $('a[href*="#"]')
    // Remove links that don't actually link to anything
    .not('[href="#"]')
    .not('[href="#0"]')
    .click(function (event) {
      // On-page links
      if (
        location.pathname.replace(/^\//, "") ==
          this.pathname.replace(/^\//, "") &&
        location.hostname == this.hostname
      ) {
        // Figure out element to scroll to
        var target = $(this.hash);
        target = target.length
          ? target
          : $("[name=" + this.hash.slice(1) + "]");
        // Does a scroll target exist?
        if (target.length) {
          // Only prevent default if animation is actually gonna happen
          event.preventDefault();
          $("html, body").animate(
            {
              scrollTop: target.offset().top,
            },
            1000,
            function () {
              // Callback after animation
              // Must change focus!
              var $target = $(target);
              $target.focus();
              if ($target.is(":focus")) {
                // Checking if the target was focused
                return false;
              } else {
                $target.attr("tabindex", "-1"); // Adding tabindex for elements not focusable
                $target.focus(); // Set focus again
              }
            }
          );
        }
      }
    });
});

